@keyframes digi-fadeUp {
  from {
    opacity: 0;
    transform: translateY(18px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.digi-fade-1 {
  opacity: 0;
  animation: digi-fadeUp 0.6s 0.05s ease forwards;
}
.digi-fade-2 {
  opacity: 0;
  animation: digi-fadeUp 0.7s 0.20s ease forwards;
}
.digi-fade-3 {
  opacity: 0;
  animation: digi-fadeUp 0.7s 0.35s ease forwards;
}
.digi-fade-4 {
  opacity: 0;
  animation: digi-fadeUp 0.7s 0.50s ease forwards;
}
.digi-fade-5 {
  opacity: 0;
  animation: digi-fadeUp 0.8s 0.70s ease forwards;
}
.digi-feature-card {
  background: #1E1E23;
  padding: 2rem 2rem 2.25rem;
  position: relative;
  overflow: hidden;
  transition: background 0.25s;
  cursor: default;
}
.digi-feature-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: linear-gradient(90deg, transparent, rgba(58,125,255,0), transparent);
  transition: background 0.3s;
}
.digi-feature-card:hover {
  background: rgba(32,33,40,0.95);
}
.digi-feature-card:hover::before {
  background: linear-gradient(90deg, transparent, rgba(58,125,255,0.5), transparent);
}
.digi-noise {
  position: fixed;
  inset: 0;
  pointer-events: none;
  z-index: 9999;
  opacity: 0.35;
  background-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 256 256' xmlns='http://www.w3.org/2000/svg'%3E%3Cfilter id='n'%3E%3CfeTurbulence type='fractalNoise' baseFrequency='0.9' numOctaves='4' stitchTiles='stitch'/%3E%3C/filter%3E%3Crect width='100%25' height='100%25' filter='url(%23n)' opacity='0.04'/%3E%3C/svg%3E");
}
